.header-two {
	color: $grey;
	.active {
		color: $black;
		a {
			color: $black;
		}
	}
}

#nav-program {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}


.program-header {
	padding-bottom: 0.5rem;
}

.program-list {
	width: calc(100% / 6 * 3);
}
.program-entry {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	&:first-of-type {
		padding-top: 0;
	}
}
.program-entry-title {
	width: 100%;
	display: flex;
	cursor: pointer;
}
.program-entry-detail {
	width: 50%;
	@include small-text();
	max-height: 0;
	overflow: hidden;
	transition: all 0.2s ease-out;
	.link {
		padding-top: 1rem;
	}
}
// add margin to body when it's being displayed
.program-entry.active {
	.program-entry-detail {
		margin: 1rem 0;
		margin-bottom: 1.5rem;
	}
}
.program-intro {
	@include small-text();
	display: flex;
	justify-content: flex-end;
	width: calc(100% / 6 * 2);
}

#schedule {
	display: flex;
	flex-direction: column;
}

.schedule-entry {
	padding: 1rem;
	display: flex;
	cursor: pointer;
}
.schedule-entry-time {
	width: calc(100% / 6 * 1);
	padding-right: 1rem;
}
.schedule-entry-body {
	width: calc(100% / 6 * 4);
}
.schedule-entry-detail {
	max-height: 0;
	overflow: hidden;
	width: calc(100% / 4 * 2);
	@include small-text();
}
.schedule-entry.active {
	.schedule-entry-detail {
		margin-top: 1rem;
		margin-bottom: -1rem;
	}
}

#info {
	padding: 1rem;
}

.info-text {
	width: calc(100% / 6 * 3);
	padding-bottom: 2rem;
	p:not(:last-of-type){
		padding-bottom: 1rem;
	}
}
// schedule
@media only screen and (max-width: $mobile) {
	.info-text,
	.program-list,
	.program-entry-detail,
	.program-intro,
	.schedule-entry,
	.schedule-entry-detail,
	.schedule-entry-body,
	.schedule-entry-time,
	.program-list {
		width: 100%;
	}

	.schedule-entry {
		flex-wrap: wrap;
	}
}
