//== Core CSS
//## General

// Hamburger menu

* {
    box-sizing: border-box;
}

html {
  @include fluid-type($min_width, $max_width, $min_font, $max_font);
    font-family: 'ABCMonument';
    -webkit-text-size-adjust:none;
    background-color: $primary;
}

i {
    font-family: 'ABCMonumentItalic';
}

body {
    font-family: 'ABCMonument', Arial, Helvetica, sans-serif;
    overflow-x: hidden;
}

.wrapper {
    padding: 0 8%;
    width: 100%;
    max-width: 1640px;
    margin: 0 auto;
    @include for-tablet-down() {
        padding: 0 5%;
    }
}

.title-header {
    width: 100%;
    text-align: center;
    letter-spacing: -1px;
	padding: 0 1rem;
    padding-bottom: 1rem;
}

.mobile {
    display: none;
    @media only screen and (max-width: $mobile) {
        display: block;
    }
}

.mobile-hide {
    @media only screen and (max-width: $mobile) {
        display: none;
    }
}