.header {
	padding-top: 1rem;
	display: flex;
	flex-direction: column;
}

.header-list-right {
	justify-content: flex-end;
	display: flex;
	h4 {
		padding-right: 1rem;
		&:hover {
			color: $black;
		}
	}
}

.fixed-center {
	display: grid;
	grid-template-columns: 1fr auto 1fr;
}

.header-one, .header-two{
	padding: 0 1rem;
}

.header-one-body {
	.title:not(.active){
		color: $grey;
	}
}
nav { 
	display: flex;
	justify-content: center;
}
nav ul {
	list-style-type: none;
	display: inline-flex;
	justify-content: center;
	margin: 0;
	padding: 0;
	li {
		padding: 0 0.5rem;
		&:first-child {
			padding-left: 0;
		}
		&:last-child {
			padding-right: 0;
		}
	}
}

.header-three {
	color: $black;
}

@media only screen and (max-width: $mobile) {

	.header-one {
		.title {
			font-size: 64px;
			line-height: 56px;
		}
		.header-list-right {
			h4 {
				display: none;
			}
		}
	}


	// .header-one-body {
	// 	.title:not(.active){
	// 		display: none;
	// 	}
	// }

	.header-two {
			display: flex;
			flex-direction: column;
			align-items: center;
			h4 {
				@include title();
			}
			.header-list-right {
				width: 100%;
				justify-content: space-between;
			}
			.header-two-main {
				order: -1;
			}
		}
}
